import React from 'react';
import { makeStyles, createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import {AppBar, Tabs, Tab, Grid} from "@material-ui/core";

import Layout from '../../Layout/Layout';
import classNames from 'classnames';




const useStyles = makeStyles({


  root: {
    padding: '0 16px',
    fontFamily: "Righteous",
  },
  primary: { 
      backgroundColor:'var(--mdc-theme-primary, #212121)',
      boxShadow: '0 2px 4px #E4A0B7',
      boxSizing: 'border-box', 
      width:'100%',
  },
  title: {
    fontFamily: " 'Roboto Mono', monospace",
    marginLeft:'40px',
  },
  customfontstyle: {
    color:'white',
    fontSize:'1.2rem',
    fontWeight: 500,
    minWidth:'20px',
    height: '60px',
    
  },
  tabsStyle: {
      marginRight:'24px',

  },



});


const NavigationItem = () => {

    const classes = useStyles();
   
    const handleChange = (event, newValue) =>{
        setSelectedTab(newValue);
    }

    const tabNameToIndex = {
        0: "home",
        1: "leetcode"
    }
    const indexToTabName = {
        home : 0,
        leetcode: 1
    }
    const [selectedTab, setSelectedTab] = React.useState(0);


    return (
        <>
            <AppBar elevation={0} className={classes.primary} position="static">
                <Grid justify={"space-between"} container>
                    <Grid>
                        <Tab className={classNames(classes.title, classes.customfontstyle)} label="Moon Lee's Website" selected/>
                    </Grid>
                    <Grid className={classes.tabsStyle}>
                        <Tabs TabIndicatorProps= {{style: {height: "6px", backgroundColor: "#F8DAE9", transition: '0s'} }} className={classes.tabsStyle} value={selectedTab} onChange={handleChange} centered>
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="Home" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="About" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="SKILLS" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="Experience" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="Education" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="Projects" />
                            <Tab className={classNames(classes.root, classes.customfontstyle)} label="NOTES" />
                        </Tabs>
                    </Grid>
                </Grid>
            </AppBar>
            <Layout tabselect={selectedTab} />
            
        </>
    )

}

export default NavigationItem;