

import React from 'react';
import {Grid, Typography, Link, makeStyles} from '@material-ui/core';
import classNames from 'classnames';

const useStyles = makeStyles( (theme) => ({
    customwidth:{
        [theme.breakpoints.down('lg')]:{
            width:'auto',
        },
        [theme.breakpoints.up('lg')]:{
            width:'1100px', 
        }
    },
}));

const Education = (props) => {
    const classes = props.modifier1;
    const classes2 = props.modifier2;
    const classes1 = useStyles();
    return (
        <Grid style={{padding: 0}}item xs={12}>
            <Grid container justify="center" style={{paddingBottom:60}} className={classNames(classes2.introtext,classes2.defaultgrey)}>

                <Typography className={classNames(classes2.heading,classes2.fontheading)} variant="h3">
                    EDUCATION<span className={classes2.defaultpink}>.</span>
                </Typography>
                <Grid  item className={classes1.customwidth}>
                    <Typography className={classNames(classes2.paragraph, classes2.educationdetailfont, classes2.lineheight)} variant="h5">
                        <strong>2014  2016:</strong> Master of Science in Electrical &#38; Computer Engineering, Univeristy of Michigan, <i>Ann Arbor</i>
                    </Typography>
                    <Typography className={classNames(classes2.paragraph, classes2.educationdetailfont, classes2.lineheight)} variant="h5">
                        <strong>2009  2014:</strong> Bachelor of Applied Science, Engineering Science + 1 Year Internship, Univeristy of Toronto
                    </Typography>
                    <Typography className={classNames(classes2.paragraph, classes2.educationdetailfont)} variant="h5">
                        <strong>Relevant Courseworks</strong>: 
                        
                        <Typography className={classNames(classes2.paragraph, classes2.fontpargraph, classes2.lineheight)} variant="h5">
                                <li>Data Structures and Algorithms</li>  
                                <li>Introduction to Programming</li>
                                <li>Microcontroller Programming</li>                   
                        </Typography>
                    </Typography>
                    <Typography className={classNames(classes2.paragraph, classes2.educationdetailfont)} variant="h5">
                        <strong>Online Courseworks</strong>: 
                        
                        
                        <Typography className={classNames(classes2.paragraph, classes2.fontpargraph, classes2.lineheight)} variant="h5">
                            <li><i>University of Michigan Continuum</i> Computational Machine Learning</li>  
                            <li><i>Udemy</i> RESTful Web Services, Java, Spring Boot, Spring MVC and JPA</li>
                            <li><i>Udemy</i> Spring Boot Microservices and Spring Cloud</li>         
                            <li><i>Udemy</i> Spring &#38; Hibernate for Beginner</li>    
                            <li><i>Udemy</i> Rest API testing</li>        
                            <li><i>Udemy</i> React - The Complete Guide</li>            
                        </Typography>
                    </Typography>
                    <Typography className={classNames(classes2.paragraph, classes2.educationdetailfont)} variant="h5">
                        <strong>Textbooks</strong>: 
                        <Typography className={classNames(classes2.paragraph, classes2.fontpargraph, classes2.lineheight)} variant="h5">
                            <li>Spring in Action, 5th Edition</li>  
                            <li>Spring Microservices in Action</li>
                            <li>Spring MVC Begineers Guide</li>         
                            <li>Core Java Volume I &#38; II, 11th Edition</li>    
                            <li>Algorithms, 4th Edition by Sedgewick</li>        
                        </Typography>
                    </Typography>
                </Grid>
            </Grid>
        </Grid>
    )

}

export default Education;
